<template>
	<div>
	<ContentHeader title="Tindakan" url="/tindakan" subTitle="Data Tindakan" />
     <div class="content-header">
		<div class="container-fluid">
			<div class="row justify-content-center">
	            <div class="col">
	            	<div class="card">
	            		<div class="card-header row d-flex align-items-center">
	            			<h3 class="card-title col-6">Table tindakan</h3>
	            			<div class="col-6 text-right">
	            				<router-link to="/tambah-data-tindakan"><button class="btn btn-sm btn-info">Tambah</button></router-link>	
	            			</div>
	            		</div>
	            		<div class="card-body">
	            			<table class="table table-bordered table-hover">
							  <thead>
							    <tr>
							      <th scope="col" class="text-sm">KODE</th>
							      <th scope="col" class="text-sm">NAMA</th>
							      <th scope="col" class="text-sm">JENIS</th>
							      <th scope="col" class="text-sm">DURASI</th>
							      <th scope="col" class="text-sm">TARIF PASIEN BARU</th>
							      <th scope="col" class="text-sm">TARIF MEMBER</th>
							      <th scope="col" class="text-sm">MODAL</th>
							      <th scope="col" class="text-sm">POINT</th>
							      <th scope="col" class="text-sm">ACTION</th>
							    </tr>
							  </thead>
							  <tbody>
							    <tr v-for="row in tindakan" :key="row.id">
							      <td class="text-sm" scope="row">{{row.kode_tindakan}}</td>
							      <td class="text-sm">{{ row.nama }}</td>
							      <td class="text-sm">{{ row.jenis_tindakan }}</td>
							      <td class="text-sm">{{ row.lama_tindakan }} Menit</td>
							      <td class="text-sm">Rp. {{ number_format(row.harga_pasien_baru, 0, ',', '.') }}</td>
							      <td class="text-sm">Rp. {{ number_format(row.harga_pasien_member, 0, ',', '.') }}</td>
							      <td class="text-sm">Rp. {{ number_format(row.modal, 0, ',', '.') }}</td>
							      <td class="text-sm">{{ row.point }}</td>
							      <td class="text-sm">
							      	<div class="btn-group">
					                    <button type="button" class="btn btn-sm btn-info text-sm">Proses</button>
					                    <button type="button" class="btn btn-sm btn-info dropdown-toggle dropdown-icon" data-toggle="dropdown" aria-expanded="false">
					                      <span class="sr-only"></span>
					                    </button>
					                    <div class="dropdown-menu" role="menu" style="">
					                      <router-link :to="`/edit-data-tindakan/${row.id}`" class="dropdown-item text-sm">Edit</router-link>
					                      <div class="dropdown-divider"></div>
					                      <button @click="hapusDataTindakan(row.id)" class="dropdown-item text-danger">Hapus</button>
					                    </div>
					                  </div>
							      </td>
							    </tr>
							  </tbody>
							</table>
							<p class="text-center mt-4">{{ cekData }}</p>
	            		</div>
	            	</div>
	            </div>
	        </div>
		</div>
	</div>
    </div>
</template>

<script>
import ContentHeader from '@/components/ContentHeader2'

import axios from 'axios'
import store from '@/store'
import { ref, onMounted, computed } from 'vue' 
import Swal from 'sweetalert2'
import number_format from '@/helper.js'
// import router from '@/router'


export default{
	components: {
		ContentHeader: ContentHeader,
	},
    setup(){
    	const tindakan = ref([])
    	const cekData = ref()
    	const user = computed(() => store.getters['auth/user'])

    	const getTindakan = async () => {
    		let response = await axios.get('api/tindakan/get/' + user.value.cabang_id)

    		if (response.data == 'kosong') {
	          cekData.value = 'Data Masih Kosong'
	          tindakan.value = null
	        } else {
	          cekData.value = ''
	          tindakan.value = response.data
	        }
    	}

    	const hapusDataTindakan = (id) => {
    		axios.delete(`api/tindakan/destroy/${id}`)
    		.then((response) => {
    			Swal.fire({
				  title: 'Berhasil!',
				  text: 'Berhasil menghapus data',
				  icon: 'success',
				  confirmButtonText: 'Ok'
				})	
    			console.log(response.data)
    		})
    		.catch((error) => {
    			Swal.fire({
				  title: 'Gagal!',
				  text: 'Gagal menghapus data',
				  icon: 'success',
				  confirmButtonText: 'Ok'
				})
    			console.log(error)
    		})

    		getTindakan()
    	}

    	// Refresh data 
    	setInterval(() => {
	    	getTindakan()		
    	}, 240000)
    	// End Refresh Data

    	onMounted(() => {
    		getTindakan()
    	})

		return {
			tindakan, getTindakan, hapusDataTindakan, number_format, cekData
		}
    }
}
</script>

